<template>
  <form @submit.prevent="onSubmit">
    <input type="text" v-model="title">
    <button type="submit">–î–æ–±–∞–≤–∏—Ç—å</button>
  </form>
</template>

<script>
export default {
  data() {
    return {
      title: ''
    }
  },

  methods: {
    onSubmit() {
      if (this.title.trim()) {
        const newTodo = {
          id: Date.now(),
          title: this.title,
          completed: false
        }

        this.$emit('add-todo', newTodo)
        this.title = ''
      }
    }
  }
}
</script>

<style lang="sass" scoped>
  form
    display: flex
  input
    min-width: 200px
    margin-right: 10px
</style>